import styles from "./OrderWorks.module.css";
import sprite from "../../icons/sprite.svg";
import Container from "../Container";
import { Button } from "react-bootstrap";

const OrderWorks = ({ handleShow }) => {
  return (
    <section id="OrderWorks" className={styles.section}>
      <h3 className={styles.title}>    ?</h3>

      <Container>
        <ul className={styles.list}>
          <li className={styles.item}>
            <svg className={styles.icon}>
              <use href={sprite + "#icon-form"} />
            </svg>
            <p className={styles.step}> 1</p>
            <p>     .</p>
            {/* <p> .  1  </p> */}
            {/* <Button variant="warning" type="button" onClick={handleShow}>
               
            </Button> */}
          </li>
          <li className={styles.item}>
            <svg className={styles.icon}>
              <use href={sprite + "#icon-calendar"} />
            </svg>
            <p className={styles.step}> 2</p>
            <p>       .</p>
            {/* <p>   </p> */}
          </li>
          <li className={styles.item}>
            <svg className={styles.icon}>
              <use href={sprite + "#icon-contract"} />
            </svg>
            <p className={styles.step}> 3</p>
            <p>      </p>
            {/* <p>
               
              <a href="https://docs.google.com/document/d/19c4PQtRJgYzGWizXa96UxW-Xm_jW247xfSfOQz2NcCI/edit?usp=sharing" alt="link" target="_blank"></a>
            </p> */}

          </li>
          <li className={styles.item}>
            <svg className={styles.icon}>
              <use href={sprite + "#icon-certificate"} />
            </svg>
            <p className={styles.step}> 4</p>
            <p>   </p>
            {/* <p>
                
              <a href="https://docs.google.com/document/d/1PrBCX_6EaCoqrsxkStFdvNsxW4_vGCJh/edit?usp=sharing&ouid=114358473608478208701&rtpof=true&sd=true" alt="link" target="_blank"></a>
            </p> */}
          </li>
        </ul>
        <div className={styles.buttonWrap}>
          <Button variant="outline-warning" type="button" onClick={handleShow}>
             
          </Button>
        </div>

      </Container>
    </section>
  );
};

export default OrderWorks;
